import { ChangeEvent, FC, useCallback, useRef } from 'react';
import classnames from 'classnames';
import uniqueId from 'lodash/uniqueId';

// Utils
import { createResizeObserverHandler, useEnhancedEffect } from './utils';
/**
 * This component is designed to progressively display content based on a set truncation point.
 * It will truncate the content at the specified line and provide a View More/View Less action
 * to toggle the content.
 * @param text The text for formatted fragment to display.
 * @param truncateAtLine The line to start the truncation.
 * @param size The size of the text.
 * @param classNames Additional CSS classes to apply to text content.
 * @param onViewMoreChanged Callback to subscribe to when view more switches between expanded and collapsed states.
 * @param expandedByDefault Whether the text should be expanded by default.
 * @returns A ViewMore component.
 *
 * With the text field, we can pass in a string or a React Fragment. If we pass in a string, it will be displayed as is.
 * If we pass in a React Fragment, we can pass in custom formatting to the text.
 * Note: You can pass in a React Fragment but this should be only formatted text inside a fragment.
 */

export interface ViewMoreProps {
  // Text to display.
  text: React.ReactFragment | string;
  /** Line to start the truncation */
  truncateAtLine?: number;
  /** Text size */
  size?: 'small' | 'regular' | 'large';
  /** Additional CSS classes to apply to text content */
  classNames?: string;
  /** Callback to subscribe to when view more switches between expanded and collapsed states */
  onViewMoreChanged?: (
    expanded: boolean,
    paragraphElement: HTMLParagraphElement,
  ) => void;
  expandedByDefault?: boolean;
}

// Components
import Container from '../Container';

// Styles
import './ViewMore.scss';

const ViewMore: FC<ViewMoreProps> = ({
  text,
  truncateAtLine = 4,
  size = 'regular',
  classNames = '',
  onViewMoreChanged,
  expandedByDefault = false,
}) => {
  const textRef = useRef<HTMLParagraphElement>(null);
  const inputId = uniqueId('input_');

  const expandLabelClasses = classnames(
    `sb-view-more__expand sb-view-more__action sb-view-more__action_${size}`,
    classNames,
  );

  const collapseLabelClasses = classnames(
    `sb-view-more__collapse sb-view-more__action sb-view-more__action_${size}`,
    classNames,
  );
  const paragraphClasses = classnames(
    `sb-view-more__text sb-view-more__text_${size}`,
    classNames,
  );

  const onChangedHandler = useCallback(
    (e: ChangeEvent<HTMLInputElement>) => {
      if (onViewMoreChanged && textRef.current) {
        onViewMoreChanged(e.target.checked, textRef.current);
      }
    },
    [onViewMoreChanged],
  );

  useEnhancedEffect(() => {
    try {
      const resizeObserverCallback = createResizeObserverHandler();
      const paragraphObserver = new ResizeObserver(resizeObserverCallback);

      if (textRef.current) {
        paragraphObserver.observe(textRef.current);
      }

      return () => {
        resizeObserverCallback.cancel();
        paragraphObserver.disconnect();
      };
    } catch {
      // If ResizeObserver is not supported, a fallback to the view more/view less actions being always visible instead of
      // only showing them when the line-clamp is applied.
      if (textRef.current) {
        textRef.current.classList.add('sb-view-more__text_truncated');
      }
    }
  }, []);

  return (
    <Container padding="none" className="sb-view-more">
      {/* Check this out for more details about the implementation details of this component: https://css-tricks.com/the-checkbox-hack/ */}
      <input
        className="sb-view-more__input"
        defaultChecked={expandedByDefault}
        id={inputId}
        onChange={onChangedHandler}
        type="checkbox"
      />

      <p
        ref={textRef}
        // eslint-disable-next-line @typescript-eslint/ban-ts-comment
        // @ts-ignore
        style={{ '--sb-view-more-line-clamp': `${truncateAtLine}` }}
        className={paragraphClasses}
        data-testid="ViewMore_Text"
      >
        {text}
      </p>

      <label
        className={expandLabelClasses}
        data-testid="ViewMore_Expand_Action"
        htmlFor={inputId}
        role="button"
      >
        View More
      </label>

      <label
        className={collapseLabelClasses}
        data-testid="ViewMore_Collapse_Action"
        htmlFor={inputId}
        role="button"
      >
        View Less
      </label>
    </Container>
  );
};

export default ViewMore;
