import { FC, useMemo } from 'react';
import classNames from 'classnames';

// Styles
import './Spinner.scss';

// Types
import {
  AnimationOptions,
  LocationOptions,
  SizeOptions,
  TestMetadata,
} from '../types';

export interface SpinnerProps {
  /** Add a class to the spinner */
  className?: string;
  /** Loading text for screen readers */
  loadText?: string;
  /** Size: smaller, small, medium (default), large */
  size?: SizeOptions;
  /** Full URL to a custom image you would like to use */
  imageUrl?: string;
  /** Animation: spin (default), bounce, rock, wobble */
  animation?: AnimationOptions;
  /** Location: mask, background */
  /** Mask can break some animations but allows you to change the color of the icon. Use background if Mask is causing problems. */
  location?: LocationOptions;
  // Test IDs for component sections
  testMetadata?: TestMetadata;
}

const Spinner: FC<SpinnerProps> = ({
  className,
  loadText = 'Loading...',
  size = 'medium',
  imageUrl,
  animation = 'spin',
  location = 'mask',
  testMetadata,
  ...otherProps
}) => {
  const spinnerClassnames = useMemo(
    () =>
      classNames(
        `sb-spinner_${location}`,
        `sb-spinner_animate_${animation}`,
        `sb-spinner_${size}`,
      ),
    [size, animation, location],
  );

  let styles = {};
  if (imageUrl) {
    if (location === 'mask') {
      styles = {
        maskImage: `url(${imageUrl})`,
        WebkitMaskImage: `url(${imageUrl})`,
      };
    } else {
      styles = {
        backgroundImage: `url(${imageUrl})`,
      };
    }
  }

  return (
    <div
      {...otherProps}
      className={classNames('sb-spinner', className)}
      role="alert"
      aria-live="assertive"
      {...testMetadata}
    >
      <p className="sb-spinner-load-text">{loadText}</p>
      <div
        className={spinnerClassnames}
        style={styles}
        data-testid="spinner-mask"
      />
    </div>
  );
};

export default Spinner;
