import { useEffect, useLayoutEffect } from 'react';
import throttle from 'lodash/throttle';

// Types
import { DebouncedFunc } from 'lodash';

export const useEnhancedEffect =
  typeof window === 'undefined' ? useEffect : useLayoutEffect;

export const createResizeObserverHandler =
  (): DebouncedFunc<ResizeObserverCallback> =>
    throttle((entries) => {
      const elementHeightOffset = 2;
      const resizeObserverEntry = entries[0];
      const shouldTruncateContent =
        resizeObserverEntry.target.scrollHeight >
        resizeObserverEntry.contentRect.height + elementHeightOffset;

      resizeObserverEntry.target.classList[
        shouldTruncateContent ? 'add' : 'remove'
      ]('sb-view-more__text_truncated');
    }, 300);
